import React, { useEffect, useState, useMemo, useCallback } from 'react';
import { useParams, useNavigate, useLocation } from 'react-router-dom';
import {
  Box, Typography, Card, CardContent, Grid, Avatar, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, TablePagination, Chip, Button, LinearProgress, Tooltip, useTheme, Snackbar, Alert, IconButton, Skeleton, TextField, InputAdornment
} from '@mui/material';
import {
  ArrowBack as ArrowBackIcon,
  Assessment as AssessmentIcon,
  PeopleAlt as PeopleIcon,
  StorefrontOutlined as StoreIcon,
  InventoryOutlined as InventoryIcon,
  Person as PersonIcon,
  ExpandMore as ExpandMoreIcon,
  ExpandLess as ExpandLessIcon,
  CalendarToday as CalendarIcon
} from '@mui/icons-material';
import { db } from '../services/firebase';
import { doc, getDoc, collection, getDocs } from 'firebase/firestore';
import { useAuth } from '../context/AuthContext';
import { getSubordinateIds, getSubordinateMarketingStaffIds, canViewStaff } from '../utils/roles';
import { Tree, TreeNode } from 'react-organizational-chart';
import { format, isToday } from 'date-fns';

// Import Google Maps components 
import { GoogleMap, useJsApiLoader, Marker, InfoWindow, Polyline } from '@react-google-maps/api';

function getInitials(name) {
  if (!name) return '?';
  return name
    .split(' ')
    .map(part => part[0])
    .join('')
    .toUpperCase()
    .substring(0, 2);
}

// Add a helper to parse Firestore Timestamp, Date, or string
function toDateSafe(val) {
  if (!val) return null;
  if (val instanceof Date) return val;
  if (val.toDate) return val.toDate();
  // Try to parse string (e.g., 'May 2, 2025 at 4:03:58 PM UTC+5:30')
  const parsed = new Date(val);
  return isNaN(parsed) ? null : parsed;
}

// Helper: Recursively collect all subordinate marketing staff IDs under a given staffId
function getAllSubordinateMarketingStaffIds(staffId, allUsers) {
  let result = [];
  const directSubs = allUsers.filter(u => u.reportingToId === staffId);
  for (const sub of directSubs) {
    if (sub.role === 'MarketingStaff') {
      result.push(sub.id);
    } else {
      result = result.concat(getAllSubordinateMarketingStaffIds(sub.id, allUsers));
    }
  }
  return result;
}

// Google Maps container styles
const mapContainerStyle = {
  width: '100%',
  height: '400px',
  borderRadius: '8px'
};

const StaffVisitHistory = () => {
  const theme = useTheme();
  const navigate = useNavigate();
  const { staffId } = useParams();
  const location = useLocation();
  const isSupervisorVisitHistory = location.state && location.state.staffRole === 'Supervisor';
  const fallbackStaff = location.state || {};
  const { userProfile, allUsers: authAllUsers, loading: authLoading } = useAuth();

  const [staff, setStaff] = useState(null);
  const [allUsers, setAllUsers] = useState([]);
  const [loading, setLoading] = useState(true);
  const [visits, setVisits] = useState([]);
  const [snackbar, setSnackbar] = useState({ open: false, message: '', severity: 'success' });
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [subordinateStats, setSubordinateStats] = useState([]);
  const [staffLoading, setStaffLoading] = useState(true);
  const [productMap, setProductMap] = useState({});
  const [retailerMap, setRetailerMap] = useState({});
  const [selectedDate, setSelectedDate] = useState(new Date().toISOString().split('T')[0]);
  const [mapCenter, setMapCenter] = useState({ lat: 20.5937, lng: 78.9629 }); // Default center at India
  const [mapZoom, setMapZoom] = useState(5);
  const [selectedMarker, setSelectedMarker] = useState(null);
  const [expandedRows, setExpandedRows] = useState({});

  // Load Google Maps API
  const { isLoaded } = useJsApiLoader({
    id: 'google-map-script',
    googleMapsApiKey: process.env.REACT_APP_GOOGLE_MAPS_API_KEY // Use environment variable for API key
  });

  // Filter visits by date
  const filteredVisits = useMemo(() => {
    if (!visits || !selectedDate) return visits;
    
    return visits.filter(visit => {
      if (!visit.checkInTime) return false;
      const visitDate = visit.checkInTime instanceof Date ? visit.checkInTime : new Date(visit.checkInTime);
      return format(visitDate, 'yyyy-MM-dd') === selectedDate;
    });
  }, [visits, selectedDate]);

  // Get map markers from filtered visits
  const mapMarkers = useMemo(() => {
    if (!filteredVisits || filteredVisits.length === 0) return [];
    
    return filteredVisits
      .filter(visit => visit.checkInLocation && visit.checkInLocation.latitude && visit.checkInLocation.longitude)
      .map(visit => ({
        id: visit.id,
        position: { lat: visit.checkInLocation.latitude, lng: visit.checkInLocation.longitude },
        retailerName: retailerMap[visit.retailerId] || retailerMap[visit.dealerId] || 'Unknown',
        checkInTime: visit.checkInTime,
        retailerId: visit.retailerId,
        dealerId: visit.dealerId,
        products: visit.products || []
      }))
      // Sort markers by check-in time to create chronological path
      .sort((a, b) => {
        const timeA = a.checkInTime instanceof Date ? a.checkInTime.getTime() : 0;
        const timeB = b.checkInTime instanceof Date ? b.checkInTime.getTime() : 0;
        return timeA - timeB;
      });
  }, [filteredVisits, retailerMap]);

  // Create path for polyline
  const mapPath = useMemo(() => {
    return mapMarkers.map(marker => marker.position);
  }, [mapMarkers]);

  // Polyline options
  const polylineOptions = {
    strokeColor: theme.palette.warning.main,
    strokeOpacity: 0.8,
    strokeWeight: 3,
    geodesic: true
  };

  // Update map center when markers change
  useEffect(() => {
    if (mapMarkers.length > 0) {
      // Calculate the average center point from all markers
      const lats = mapMarkers.map(m => m.position.lat);
      const lngs = mapMarkers.map(m => m.position.lng);
      
      const avgLat = lats.reduce((sum, lat) => sum + lat, 0) / lats.length;
      const avgLng = lngs.reduce((sum, lng) => sum + lng, 0) / lngs.length;
      
      setMapCenter({ lat: avgLat, lng: avgLng });
      
      // Set appropriate zoom level based on number of markers
      if (mapMarkers.length === 1) {
        setMapZoom(15); // Close zoom for single marker
      } else {
        // More markers need a wider view
        setMapZoom(12);
      }
    }
  }, [mapMarkers]);

  // Fetch staff and all users
  useEffect(() => {
    setStaffLoading(true);
    setLoading(true);
    const fetchData = async () => {
      try {
        // Fetch staff
        const staffDoc = await getDoc(doc(db, 'users', staffId));
        if (!staffDoc.exists()) {
          setSnackbar({ open: true, message: 'Staff not found', severity: 'error' });
          setStaff(null);
          setStaffLoading(false);
          setLoading(false);
          return;
        }
        setStaff({ id: staffDoc.id, ...staffDoc.data() });
        setStaffLoading(false);
        // Use allUsers from AuthContext
        setAllUsers(authAllUsers || []);
      } catch (err) {
        setSnackbar({ open: true, message: err.message || 'Error loading staff', severity: 'error' });
        setStaffLoading(false);
      } finally {
        setLoading(false);
      }
    };
    fetchData();
  }, [staffId, userProfile, authAllUsers]);

  // Fetch visits if marketing staff or supervisor
  useEffect(() => {
    if (!staff) return;
    // Supervisor logic
    if (location.state && location.state.staffRole === 'Supervisor') {
      setLoading(true);
      const fetchDealerVisits = async () => {
        try {
          const dealerVisitIds = location.state.dealerVisitIds || [];
          if (!Array.isArray(dealerVisitIds) || dealerVisitIds.length === 0) {
            setVisits([]);
            setLoading(false);
            return;
          }
          const visitDocs = await Promise.all(
            dealerVisitIds.map(id => getDoc(doc(db, 'dealerVisits', id)))
          );
          const visitsData = visitDocs
            .filter(docSnap => docSnap.exists())
            .map(docSnap => {
              const data = docSnap.data();
              return {
                ...data,
                id: docSnap.id,
                checkInTime: toDateSafe(data.checkInTime),
                checkOutTime: toDateSafe(data.checkOutTime),
              };
            });
          setVisits(visitsData);
        } catch (err) {
          setSnackbar({ open: true, message: err.message || 'Error loading dealer visits', severity: 'error' });
        } finally {
          setLoading(false);
        }
      };
      fetchDealerVisits();
      return;
    }
    // MarketingStaff logic (existing)
    if (staff.role !== 'MarketingStaff') return;
    setLoading(true);
    const fetchVisits = async () => {
      try {
        const visitIds = staff.visitIds || [];
        if (!Array.isArray(visitIds) || visitIds.length === 0) {
          setVisits([]);
          setLoading(false);
          return;
        }
        const visitDocs = await Promise.all(
          visitIds.map(id => getDoc(doc(db, 'visits', id)))
        );
        const visitsData = visitDocs
          .filter(docSnap => docSnap.exists())
          .map(docSnap => {
            const data = docSnap.data();
            return {
              ...data,
              id: docSnap.id,
              checkInTime: toDateSafe(data.checkInTime),
              checkOutTime: toDateSafe(data.checkOutTime),
            };
          });
        setVisits(visitsData);
      } catch (err) {
        setSnackbar({ open: true, message: err.message || 'Error loading visits', severity: 'error' });
      } finally {
        setLoading(false);
      }
    };
    fetchVisits();
  }, [staff, location.state]);

  // Fetch subordinate stats if not marketing staff
  useEffect(() => {
    if (!staff || staff.role === 'MarketingStaff') return;
    setLoading(true);
    const fetchSubordinateStats = async () => {
      try {
        // Get direct subordinates (fix: use reportingToId)
        const directSubs = allUsers.filter(u => u.reportingToId === staff.id);
        // For each, get stats (recursively aggregate for non-marketing staff)
        const stats = await Promise.all(directSubs.map(async (sub) => {
          let visitCount = 0;
          let retailersReached = 0;
          let cartonsSold = 0;
          let totalSalesAmount = 0;
          let marketingStaffIds = [];
          if (sub.role === 'MarketingStaff') {
            marketingStaffIds = [sub.id];
          } else {
            marketingStaffIds = getAllSubordinateMarketingStaffIds(sub.id, allUsers);
          }
          if (marketingStaffIds.length > 0) {
            // Fetch all visits for all subordinate marketing staff
            let allVisitIds = [];
            for (const msId of marketingStaffIds) {
              const ms = allUsers.find(u => u.id === msId);
              if (ms && Array.isArray(ms.visitIds)) {
                allVisitIds = allVisitIds.concat(ms.visitIds);
              }
            }
            if (allVisitIds.length > 0) {
              const visitDocs = await Promise.all(
                allVisitIds.map(id => getDoc(doc(db, 'visits', id)))
              );
              visitCount = visitDocs.length;
              retailersReached = new Set(visitDocs.map(v => v.data()?.retailerId)).size;
              
              // Calculate both cartons sold and total sales amount
              visitDocs.forEach(visit => {
                const visitData = visit.data();
                const products = visitData?.products || [];
                
                products.forEach(product => {
                  const count = parseInt(product.count) || 0;
                  cartonsSold += count;
                  
                  // Calculate sales amount based on product rate
                  if (product.rate) {
                    totalSalesAmount += count * parseFloat(product.rate);
                  }
                });
              });
            }
          }
          return {
            id: sub.id,
            name: sub.name,
            role: sub.role,
            status: sub.active ? 'Active' : 'Inactive',
            visitCount,
            retailersReached,
            cartonsSold,
            totalSalesAmount
          };
        }));
        setSubordinateStats(stats);
      } catch (err) {
        setSnackbar({ open: true, message: err.message || 'Error loading subordinate stats', severity: 'error' });
      } finally {
        setLoading(false);
      }
    };
    fetchSubordinateStats();
  }, [staff, allUsers]);

  // Fetch all products for name lookup
  useEffect(() => {
    const fetchProducts = async () => {
      try {
        const productsCollection = collection(db, 'products');
        const productSnapshot = await getDocs(productsCollection);
        const pMap = {};
        productSnapshot.docs.forEach(doc => {
          const d = doc.data();
          pMap[doc.id] = d;
        });
        setProductMap(pMap);
      } catch (error) {
        setProductMap({});
      }
    };
    fetchProducts();
  }, []);

  // Fetch only required retailers
  useEffect(() => {
    // Collect unique retailerIds or dealerIds from visits
    let ids = [];
    if (isSupervisorVisitHistory) {
      ids = Array.from(new Set(visits.map(v => v.dealerId).filter(Boolean)));
    } else {
      ids = Array.from(new Set(visits.map(v => v.retailerId).filter(Boolean)));
    }
    if (ids.length === 0) {
      setRetailerMap({});
      return;
    }
    // Firestore 'in' queries support up to 10 items per query
    const fetchEntitiesByIds = async () => {
      try {
        let rMap = {};
        for (let i = 0; i < ids.length; i += 10) {
          const batchIds = ids.slice(i, i + 10);
          const q = batchIds.map(id => getDoc(doc(db, isSupervisorVisitHistory ? 'dealers' : 'retailers', id)));
          const docs = await Promise.all(q);
          docs.forEach(docSnap => {
            if (docSnap.exists()) {
              const d = docSnap.data();
              rMap[docSnap.id] = d.name;
            }
          });
        }
        setRetailerMap(rMap);
      } catch (error) {
        setRetailerMap({});
      }
    };
    fetchEntitiesByIds();
  }, [visits, isSupervisorVisitHistory]);

  // Access control: only allow if user can view this staff
  const canView = useMemo(() => {
    if (!userProfile || !staff) return undefined; // undefined means still loading
    if (userProfile.role === 'Admin') return true;
    // Use getSubordinateIds to check if staffId is in user's scope
    const myId = userProfile.id;
    const allowedIds = getSubordinateIds(myId, allUsers);
    return staff.id === myId || allowedIds.includes(staff.id);
  }, [userProfile, staff, allUsers]);

  // Helper to build the hierarchy tree recursively
  function buildHierarchyTree(staffId, allUsers) {
    const staff = allUsers.find(u => u.id === staffId);
    if (!staff) return null;
    const subordinates = allUsers.filter(u => u.reportingToId === staffId);
    return {
      ...staff,
      subordinates: subordinates.map(sub => buildHierarchyTree(sub.id, allUsers)).filter(Boolean)
    };
  }

  // Helper to render the tree nodes
  function renderTreeNode(node, theme) {
    if (!node) return null;
    return (
      <TreeNode
        label={
          <Box sx={{ display: 'flex', alignItems: 'center', bgcolor: theme.palette.warning.light, color: theme.palette.warning.contrastText || '#fff', borderRadius: 2, px: 2, py: 1, boxShadow: 1 }}>
            <Avatar sx={{ bgcolor: theme.palette.warning.main, color: '#fff', width: 36, height: 36, mr: 1 }}>{getInitials(node.name)}</Avatar>
            <Box>
              <Typography variant="subtitle2" fontWeight={600}>{node.name}</Typography>
              <Typography variant="caption" color="text.secondary">{node.role}</Typography>
            </Box>
          </Box>
        }
      >
        {Array.isArray(node.subordinates) && node.subordinates.length > 0 && node.subordinates.map(sub => renderTreeNode(sub, theme))}
      </TreeNode>
    );
  }
  
  const handleToggleExpand = (visitId) => {
    setExpandedRows((prev) => ({ ...prev, [visitId]: !prev[visitId] }));
  };
  
  // Handle marker click to show InfoWindow
  const handleMarkerClick = (marker) => {
    setSelectedMarker(marker);
  };

  // Always render the main layout, but handle loading/unauthorized
  if (authLoading || staffLoading || canView === undefined) {
    // Show header with fallbackStaff if available, otherwise skeletons
    return (
      <Box sx={{ p: 3 }}>
        <Card sx={{ mb: 4, borderRadius: 2, boxShadow: theme.shadows[2] }}>
          <CardContent sx={{ p: 3 }}>
            <Grid container spacing={2}>
              <Grid item xs={12} md={6}>
                <Box sx={{ display: 'flex', alignItems: 'center' }}>
                  {fallbackStaff.name ? (
                    <>
                      <Skeleton variant="circular" width={60} height={60} sx={{ mr: 2 }} />
                      <Box>
                        <Typography variant="h5" gutterBottom fontWeight="bold">{fallbackStaff.name}</Typography>
                        <Typography variant="body2" color="text.secondary">{fallbackStaff.email}</Typography>
                        <Typography variant="body2" color="text.secondary">{fallbackStaff.phoneNumber}</Typography>
                      </Box>
                    </>
                  ) : (
                    <>
                      <Skeleton variant="circular" width={60} height={60} sx={{ mr: 2 }} />
                      <Box sx={{ flex: 1 }}>
                        <Skeleton variant="text" width={180} height={32} sx={{ mb: 1 }} />
                        <Skeleton variant="text" width={120} height={20} sx={{ mb: 0.5 }} />
                        <Skeleton variant="text" width={100} height={20} />
                      </Box>
                    </>
                  )}
                </Box>
              </Grid>
              <Grid item xs={12} md={6}>
                <Box sx={{ display: 'flex', justifyContent: { xs: 'flex-start', md: 'flex-end' }, gap: 3, mt: { xs: 2, md: 0 }, height: '100%', alignItems: 'center' }}>
                  <Box sx={{ textAlign: { xs: 'left', md: 'center' } }}>
                    <Skeleton variant="text" width={80} height={36} />
                    <Skeleton variant="text" width={60} height={20} />
                  </Box>
                </Box>
              </Grid>
            </Grid>
          </CardContent>
        </Card>
        <Card sx={{ borderRadius: 2, boxShadow: theme.shadows[2] }}>
          <CardContent sx={{ p: 3 }}>
            <LinearProgress sx={{ mt: 2, mb: 1, borderRadius: 1 }} />
          </CardContent>
        </Card>
      </Box>
    );
  }

  if (canView === false) {
    return (
      <Box sx={{ p: 4, textAlign: 'center' }}>
        <Typography variant="h6" color="error">You are not authorized to view this staff's data.</Typography>
        <Button variant="contained" onClick={() => navigate(-1)} sx={{ mt: 2 }} startIcon={<ArrowBackIcon />}>Back</Button>
      </Box>
    );
  }

  // Main content (as before)
  const visitEntityLabel = isSupervisorVisitHistory ? 'Dealer' : 'Retailer';
  return (
    <Box sx={{ p: 3 }}>
      {/* Header Card: Staff Info */}
      <Card sx={{ mb: 4, borderRadius: 2, boxShadow: theme.shadows[2] }}>
        <CardContent sx={{ p: 3 }}>
          <Grid container spacing={2}>
            <Grid item xs={12} md={6}>
              <Box sx={{ display: 'flex', alignItems: 'center' }}>
                {staffLoading ? (
                  fallbackStaff.name ? (
                    <>
                      <Skeleton variant="circular" width={60} height={60} sx={{ mr: 2 }} />
                      <Box>
                        <Typography variant="h5" gutterBottom fontWeight="bold">{fallbackStaff.name}</Typography>
                        <Typography variant="body2" color="text.secondary">{fallbackStaff.email}</Typography>
                        <Typography variant="body2" color="text.secondary">{fallbackStaff.phoneNumber}</Typography>
                      </Box>
                    </>
                  ) : (
                    <>
                      <Skeleton variant="circular" width={60} height={60} sx={{ mr: 2 }} />
                      <Box sx={{ flex: 1 }}>
                        <Skeleton variant="text" width={180} height={32} sx={{ mb: 1 }} />
                        <Skeleton variant="text" width={120} height={20} sx={{ mb: 0.5 }} />
                        <Skeleton variant="text" width={100} height={20} />
                      </Box>
                    </>
                  )
                ) : staff ? (
                  <>
                    <Avatar sx={{ bgcolor: theme.palette.warning.light, color: theme.palette.warning.contrastText || '#fff', width: 60, height: 60, mr: 2, fontSize: '1.5rem' }}>
                      <PersonIcon sx={{ fontSize: 30 }} />
                    </Avatar>
                    <Box>
                      <Typography variant="h5" gutterBottom fontWeight="bold">{staff.name}</Typography>
                      <Typography variant="body2" color="text.secondary">{staff.email}</Typography>
                      <Typography variant="body2" color="text.secondary">{staff.phoneNumber}</Typography>
                    </Box>
                  </>
                ) : null}
              </Box>
            </Grid>
            <Grid item xs={12} md={6}>
              <Box sx={{ display: 'flex', justifyContent: { xs: 'flex-start', md: 'flex-end' }, gap: 3, mt: { xs: 2, md: 0 }, height: '100%', alignItems: 'center' }}>
                <Box sx={{ textAlign: { xs: 'left', md: 'center' } }}>
                  {staff && staff.role === 'MarketingStaff' ? (
                    <>
                    </>
                  ) : null}
                </Box>
              </Box>
            </Grid>
          </Grid>
        </CardContent>
      </Card>

      {/* Main Content: Visits or Subordinate Stats */}
      {(staff && (staff.role === 'MarketingStaff' || isSupervisorVisitHistory)) ? (
        <>
          {/* Map Card */}
          <Card sx={{ borderRadius: 2, boxShadow: theme.shadows[2], mb: 4 }}>
            <CardContent sx={{ p: 3 }}>
              <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
                <Typography variant="h6" fontWeight="bold">
                  <StoreIcon sx={{ mr: 1, verticalAlign: 'text-bottom' }} />
                  Visit Locations
                </Typography>
                <TextField
                  label="Select Date"
                  type="date"
                  size="small"
                  value={selectedDate}
                  onChange={(e) => setSelectedDate(e.target.value)}
                  InputLabelProps={{ shrink: true }}
                  sx={{ minWidth: 180 }}
                  InputProps={{
                    startAdornment: (
                      <InputAdornment position="start">
                        <CalendarIcon color="action" />
                      </InputAdornment>
                    ),
                  }}
                />
              </Box>
              
              {loading ? (
                <LinearProgress sx={{ mt: 2, mb: 1, borderRadius: 1 }} />
              ) : mapMarkers.length === 0 ? (
                <Box sx={{ p: 3, textAlign: 'center', backgroundColor: 'rgba(0,0,0,0.02)' }}>
                  <Typography variant="body1" color="text.secondary">No location data available for selected date</Typography>
                </Box>
              ) : isLoaded ? (
                <Box sx={{ height: 400, width: '100%', mb: 2 }}>
                  <GoogleMap
                    mapContainerStyle={mapContainerStyle}
                    center={mapCenter}
                    zoom={mapZoom}
                    options={{
                      mapTypeControl: false,
                      streetViewControl: false,
                      fullscreenControl: true,
                    }}
                  >
                    {/* Add polyline to connect markers */}
                    {mapPath.length > 1 && (
                      <Polyline
                        path={mapPath}
                        options={polylineOptions}
                      />
                    )}
                    
                    {mapMarkers.map((marker, index) => (
                      <Marker 
                        key={marker.id} 
                        position={marker.position}
                        onClick={() => handleMarkerClick(marker)}
                        label={index > 0 ? `${index + 1}` : undefined}
                      />
                    ))}
                    
                    {selectedMarker && (
                      <InfoWindow
                        position={selectedMarker.position}
                        onCloseClick={() => setSelectedMarker(null)}
                      >
                        <Box sx={{ p: 1 }}>
                          <Typography variant="subtitle2" fontWeight="bold">{selectedMarker.retailerName}</Typography>
                          <Typography variant="body2">
                            {selectedMarker.checkInTime instanceof Date 
                              ? format(selectedMarker.checkInTime, 'MMM d, yyyy, h:mm a') 
                              : 'Unknown time'}
                          </Typography>
                          {selectedMarker.products.length > 0 && (
                            <Typography variant="caption" display="block" sx={{ mt: 1 }}>
                              Products: {selectedMarker.products.length}
                            </Typography>
                          )}
                        </Box>
                      </InfoWindow>
                    )}
                  </GoogleMap>
                </Box>
              ) : (
                <Box sx={{ p: 3, textAlign: 'center', backgroundColor: 'rgba(0,0,0,0.02)' }}>
                  <Typography variant="body1" color="text.secondary">Loading Google Maps...</Typography>
                </Box>
              )}
              
              <Box sx={{ display: 'flex', justifyContent: 'space-between', mt: 2 }}>
                <Typography variant="body2" color="text.secondary">
                  Showing {mapMarkers.length} location{mapMarkers.length !== 1 ? 's' : ''} for {selectedDate ? format(new Date(selectedDate), 'MMMM d, yyyy') : 'today'}
                </Typography>
                {mapMarkers.length > 0 && (
                  <Button 
                    size="small" 
                    variant="outlined"
                    onClick={() => {
                      if (mapMarkers.length === 1) {
                        setMapCenter(mapMarkers[0].position);
                        setMapZoom(15);
                      } else if (mapMarkers.length > 1) {
                        // Recenter to the average position
                        const lats = mapMarkers.map(m => m.position.lat);
                        const lngs = mapMarkers.map(m => m.position.lng);
                        const avgLat = lats.reduce((sum, lat) => sum + lat, 0) / lats.length;
                        const avgLng = lngs.reduce((sum, lng) => sum + lng, 0) / lngs.length;
                        setMapCenter({ lat: avgLat, lng: avgLng });
                        setMapZoom(12);
                      }
                    }}
                    sx={{ borderRadius: '2rem' }}
                  >
                    Fit to Markers
                  </Button>
                )}
              </Box>
            </CardContent>
          </Card>
          
          {/* Visit Records Card */}
          <Card sx={{ borderRadius: 2, boxShadow: theme.shadows[2] }}>
            <CardContent sx={{ p: 3 }}>
              <Box sx={{ mt: 0 }}>
                <Typography variant="h6" fontWeight="bold" sx={{ mb: 2 }}>
                  <InventoryIcon sx={{ mr: 1, verticalAlign: 'text-bottom' }} />
                  Visit Records
                </Typography>
                {loading ? (
                  <LinearProgress sx={{ mt: 2, mb: 1, borderRadius: 1 }} />
                ) : filteredVisits.length === 0 ? (
                  <Box sx={{ p: 3, textAlign: 'center', backgroundColor: 'rgba(0,0,0,0.02)' }}>
                    <Typography variant="body1" color="text.secondary">No visit data available for selected date</Typography>
                  </Box>
                ) : (
                  <TableContainer>
                    <Table>
                      <TableHead>
                        <TableRow>
                          <TableCell sx={{ fontWeight: 'bold' }}>Check-in</TableCell>
                          <TableCell sx={{ fontWeight: 'bold' }}>Check-out</TableCell>
                          <TableCell sx={{ fontWeight: 'bold' }}>Duration</TableCell>
                          <TableCell sx={{ fontWeight: 'bold' }}>{visitEntityLabel}</TableCell>
                          <TableCell sx={{ fontWeight: 'bold' }}>Notes</TableCell>
                          <TableCell sx={{ fontWeight: 'bold' }}>Products Sold</TableCell>
                        </TableRow>
                      </TableHead>
                      <TableBody>
                        {filteredVisits.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((visit) => [
                          <TableRow key={visit.id} sx={{ '&:hover': { backgroundColor: 'rgba(0,0,0,0.04)' } }}>
                            <TableCell>
                              {visit.checkInTime instanceof Date && !isNaN(visit.checkInTime) ? format(visit.checkInTime, 'MMM d, yyyy, h:mm a') : 'N/A'}
                            </TableCell>
                            <TableCell>
                              {visit.checkOutTime instanceof Date && !isNaN(visit.checkOutTime) ? format(visit.checkOutTime, 'MMM d, yyyy, h:mm a') : 'N/A'}
                            </TableCell>
                            <TableCell>
                              {visit.checkInTime instanceof Date && !isNaN(visit.checkInTime) && 
                               visit.checkOutTime instanceof Date && !isNaN(visit.checkOutTime) ? 
                                (() => {
                                  const durationMs = visit.checkOutTime.getTime() - visit.checkInTime.getTime();
                                  const minutes = Math.floor(durationMs / 60000);
                                  if (minutes < 60) {
                                    return `${minutes} min${minutes !== 1 ? 's' : ''}`;
                                  } else {
                                    const hours = Math.floor(minutes / 60);
                                    const remainingMins = minutes % 60;
                                    return `${hours} hr${hours !== 1 ? 's' : ''} ${remainingMins} min${remainingMins !== 1 ? 's' : ''}`;
                                  }
                                })() : 'N/A'}
                            </TableCell>
                            <TableCell>{retailerMap[visit.retailerId] || retailerMap[visit.dealerId] || 'N/A'}</TableCell>
                            <TableCell>{visit.notes || ''}</TableCell>
                            <TableCell>
                              {Array.isArray(visit.products) && visit.products.length > 0 ? (
                                <>
                                  <Box sx={{ display: 'flex', alignItems: 'center' }}>
                                    <Typography variant="body2" sx={{ mr: 1 }}>
                                      {expandedRows[visit.id]
                                        ? ''
                                        : `${visit.products.length} product${visit.products.length > 1 ? 's' : ''}`}
                                    </Typography>
                                    <IconButton
                                      size="small"
                                      onClick={() => handleToggleExpand(visit.id)}
                                      aria-label={expandedRows[visit.id] ? 'Collapse products' : 'Expand products'}
                                    >
                                      {expandedRows[visit.id] ? <ExpandLessIcon /> : <ExpandMoreIcon />}
                                    </IconButton>
                                  </Box>
                                </>
                              ) : '—'}
                            </TableCell>
                          </TableRow>,
                          expandedRows[visit.id] && (
                            <TableRow key={`${visit.id}-expanded`}>
                              <TableCell colSpan={5} sx={{ py: 0, borderBottom: 'none' }}>
                                <Box sx={{ py: 2 }}>
                                  <Table size="small">
                                    <TableHead>
                                      <TableRow>
                                        <TableCell sx={{ fontWeight: 600, color: theme.palette.warning.main, borderBottom: 'none', py: 0.5 }}>Product Name</TableCell>
                                        <TableCell sx={{ fontWeight: 600, color: theme.palette.warning.main, borderBottom: 'none', py: 0.5 }}>Count</TableCell>
                                      </TableRow>
                                    </TableHead>
                                    <TableBody>
                                      {visit.products.map((product, idx) => (
                                        <TableRow key={idx}>
                                          <TableCell sx={{ borderBottom: 'none', py: 0.5 }}>
                                            {productMap[product.productId]?.name || 'Product'}
                                          </TableCell>
                                          <TableCell sx={{ borderBottom: 'none', py: 0.5 }}>
                                            {product.count}
                                          </TableCell>
                                        </TableRow>
                                      ))}
                                    </TableBody>
                                  </Table>
                                </Box>
                              </TableCell>
                            </TableRow>
                          )
                        ])}
                      </TableBody>
                    </Table>
                  </TableContainer>
                )}
                <TablePagination
                  rowsPerPageOptions={[5, 10, 25]}
                  component="div"
                  count={filteredVisits.length}
                  rowsPerPage={rowsPerPage}
                  page={page}
                  onPageChange={(e, newPage) => setPage(newPage)}
                  onRowsPerPageChange={e => { setRowsPerPage(parseInt(e.target.value, 10)); setPage(0); }}
                />
              </Box>
            </CardContent>
          </Card>
        </>
      ) : staff && staff.role !== 'MarketingStaff' ? (
        <Card sx={{ borderRadius: 2, boxShadow: theme.shadows[2], mb: 4 }}>
          <CardContent sx={{ p: 3 }}>
            <Typography variant="h6" fontWeight="bold" sx={{ mb: 2 }}>
              <AssessmentIcon sx={{ mr: 1, verticalAlign: 'text-bottom' }} />
              Direct Subordinates
            </Typography>
            {loading ? (
              <LinearProgress sx={{ mt: 2, mb: 1, borderRadius: 1 }} />
            ) : subordinateStats.length === 0 ? (
              <Box sx={{ p: 3, textAlign: 'center', backgroundColor: 'rgba(0,0,0,0.02)' }}>
                <Typography variant="body1" color="text.secondary">No direct subordinates found</Typography>
              </Box>
            ) : (
              <TableContainer>
                <Table>
                  <TableHead>
                    <TableRow>
                      <TableCell sx={{ fontWeight: 'bold' }}>Name</TableCell>
                      <TableCell sx={{ fontWeight: 'bold' }}>Role</TableCell>
                      <TableCell sx={{ fontWeight: 'bold' }}>Status</TableCell>
                      <TableCell sx={{ fontWeight: 'bold' }}>Visits</TableCell>
                      <TableCell sx={{ fontWeight: 'bold' }}>Retailers Reached</TableCell>
                      <TableCell sx={{ fontWeight: 'bold' }}>Cartons Sold</TableCell>
                      <TableCell sx={{ fontWeight: 'bold' }}>Sales Amount</TableCell>
                      <TableCell align="right" sx={{ fontWeight: 'bold' }}>Actions</TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {subordinateStats.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((sub) => (
                      <TableRow key={sub.id} sx={{ '&:hover': { backgroundColor: 'rgba(0,0,0,0.04)' } }}>
                        <TableCell>
                          <Box sx={{ display: 'flex', alignItems: 'center' }}>
                            <Avatar sx={{ bgcolor: theme.palette.warning.light, color: theme.palette.warning.contrastText || '#fff', width: 36, height: 36, mr: 2, fontSize: '0.9rem' }}>{getInitials(sub.name)}</Avatar>
                            <Typography variant="subtitle2">{sub.name}</Typography>
                          </Box>
                        </TableCell>
                        <TableCell>{sub.role}</TableCell>
                        <TableCell>
                          <Chip label={sub.status} color={sub.status === 'Active' ? 'success' : 'default'} size="small" sx={{ borderRadius: '14px', fontWeight: 500 }} />
                        </TableCell>
                        <TableCell>{sub.visitCount}</TableCell>
                        <TableCell>{sub.retailersReached}</TableCell>
                        <TableCell>{sub.cartonsSold}</TableCell>
                        <TableCell>
                          <Typography variant="body2" fontWeight={600} color={theme.palette.success.main}>
                            ₹{sub.totalSalesAmount.toFixed(2)}
                          </Typography>
                        </TableCell>
                        <TableCell align="right">
                          <Tooltip title="View Details">
                            <IconButton onClick={() => navigate(`/staff/${sub.id}/visits`)} size="small" sx={{ ml: 1, color: theme.palette.warning.main, bgcolor: 'rgba(255, 152, 0, 0.08)', '&:hover': { bgcolor: 'rgba(255, 152, 0, 0.12)' } }}>
                              <AssessmentIcon fontSize="small" />
                            </IconButton>
                          </Tooltip>
                        </TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </TableContainer>
            )}
            <TablePagination
              rowsPerPageOptions={[5, 10, 25]}
              component="div"
              count={subordinateStats.length}
              rowsPerPage={rowsPerPage}
              page={page}
              onPageChange={(e, newPage) => setPage(newPage)}
              onRowsPerPageChange={e => { setRowsPerPage(parseInt(e.target.value, 10)); setPage(0); }}
            />
          </CardContent>
        </Card>
      ) : null}
      <Button variant="outlined" onClick={() => navigate(-1)} sx={{ mt: 3 }} startIcon={<ArrowBackIcon />}>Back</Button>
      <Snackbar
        open={snackbar.open}
        autoHideDuration={6000}
        onClose={() => setSnackbar({ ...snackbar, open: false })}
        anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
      >
        <Alert
          onClose={() => setSnackbar({ ...snackbar, open: false })}
          severity={snackbar.severity}
          sx={{ width: '100%', boxShadow: theme.shadows[3], borderRadius: 2 }}
        >
          {snackbar.message}
        </Alert>
      </Snackbar>
    </Box>
  );
};

export default StaffVisitHistory;